// #region [Functions]=================================================================================================
/**
 * Generic WP AJAX jQuery promise.
 *
 * @param data IGenericAjaxData
 */
export var wpAjax = function (data) {
    return jQuery.ajax({
        method: "post",
        url: ajaxurl,
        data: data,
        dataType: "json"
    });
};
/**
 * WP AJAX jQuery promise to fetch license data.
 *
 * @param data any
 */
export var getLicense = function () {
    return jQuery.ajax({
        url: ajaxurl,
        data: { action: "acfw_get_license_details" },
        dataType: "json"
    });
};
/**
 * WP AJAX jQuery promise to activate license data.
 *
 * @param data ILicenseData
 */
export var activateLicense = function (data) {
    var _formNonce = acfwAdminApp.license_page._formNonce;
    data.action = "acfw_activate_license";
    return jQuery.ajax({
        method: "post",
        url: ajaxurl,
        data: {
            action: "acfw_activate_license",
            'activation-email': data.email,
            'license-key': data.key,
            'ajax-nonce': _formNonce
        },
        dataType: "json"
    });
};
// #endregion [Functions]
